package dev.compilekernel.wolf_club.common.server

import com.varabyte.kobweb.browser.http.AbortController
import com.varabyte.kobweb.browser.http.FetchDefaults
import com.varabyte.kobweb.browser.http.HttpMethod
import com.varabyte.kobweb.browser.http.ResponseException
import com.varabyte.kobweb.browser.http.fetch
import org.w3c.dom.Window
import org.w3c.fetch.RequestRedirect

private var httpUsername: String? = null
private var httpPassword: String? = null

fun setHttpCredentialsBasicAuth(username: String, password: String) {
    httpUsername = username
    httpPassword = password
}

fun clearHttpCredentialsBasicAuth() {
    httpUsername = null
    httpPassword = null
}

/**
 * @throws ResponseException When the response is not the 200 (OK) ranges.
 * */
suspend fun Window.fetchServer(
    method: HttpMethod = HttpMethod.GET,
    endpoint: String,
    headers: Map<String, Any>? = null,
    body: ByteArray? = null,
    redirect: RequestRedirect? = FetchDefaults.Redirect,
    abortController: AbortController? = null,
): String {
    val credentials = "$httpUsername:$httpPassword"
    val encodedCredentials = window.btoa(credentials)

    val response =
        window.fetch(
            method, ServerUrlBuilder.buildRequestUrl(endpoint), mapOf<String, Any>(
                "Authorization" to "Basic $encodedCredentials"
            ) + (headers ?: emptyMap()), body, redirect, abortController
        )
            .decodeToString()
    return response
}