@Page
@Composable
fun NotFoundPage() {
    // TODO: Improve design?

    val strings = LocaleMode.strings()
    PageLayout(strings.pageNotFound) {
        Column(Modifier.padding(20.px).fillMaxSize(), horizontalAlignment = Alignment.CenterHorizontally) {
            H1(
                Modifier.fontSize(7.5.cssRem)
                    .margin(0.px)
                    .fontWeight(FontWeight.Bold)
                    .toAttrs()
            ) { Text("404") }
            P { Text(strings.pageNotFoundMsg) }

            LinkButton("/", text = strings.backToHomepage, modifier = Modifier.padding(12.px))
        }
    }
}
