import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    AppGlobals.initialize(mapOf("title" to "Wolf Club"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { dev.compilekernel.wolf_club.pages.HomePage() }
        ctx.router.register("/404") { dev.compilekernel.wolf_club.pages.NotFoundPage() }
        ctx.router.register("/full-body-strength-workout") {
                dev.compilekernel.wolf_club.pages.FullBodyStrengthWorkoutPage() }
        ctx.router.register("/login") { dev.compilekernel.wolf_club.pages.LoginPage() }
        ctx.router.register("/strength-training-basics") {
                dev.compilekernel.wolf_club.pages.StrengthTrainingBasicsPage() }
        ctx.router.register("/upper-body-strength-routine") {
                dev.compilekernel.wolf_club.pages.UpperBodyStrengthRoutinePage() }
        ctx.router.register("/yoga-for-beginners") {
                dev.compilekernel.wolf_club.pages.YogaForBeginnersPage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("headline-text", dev.compilekernel.wolf_club.HeadlineTextStyle)
        ctx.theme.registerStyle("subheadline-text",
                dev.compilekernel.wolf_club.SubheadlineTextStyle)
        ctx.theme.registerStyle("markdown",
                dev.compilekernel.wolf_club.common.components.layouts.MarkdownStyle)
        ctx.theme.registerStyle("page-content",
                dev.compilekernel.wolf_club.common.components.layouts.PageContentStyle)
        ctx.theme.registerStyle("footer",
                dev.compilekernel.wolf_club.common.components.sections.FooterStyle)
        ctx.theme.registerStyle("nav-header",
                dev.compilekernel.wolf_club.common.components.sections.NavHeaderStyle)
        ctx.theme.registerStyle("circular-progress-indicator",
                dev.compilekernel.wolf_club.common.components.widgets.CircularProgressIndicatorStyle)
        ctx.theme.registerStyle("hover-link",
                dev.compilekernel.wolf_club.common.components.widgets.HoverLinkStyle)
        ctx.theme.registerVariant("-circle", dev.compilekernel.wolf_club.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", dev.compilekernel.wolf_club.UncoloredButtonVariant
        )
        ctx.theme.registerVariant("-button",
                dev.compilekernel.wolf_club.common.components.widgets.LinkButtonVariant
        )
        ctx.theme.registerKeyframes("side-menu-slide-in",
                dev.compilekernel.wolf_club.common.components.sections.SideMenuSlideInAnim)
        ctx.theme.registerKeyframes("progress-spin",
                dev.compilekernel.wolf_club.common.components.widgets.ProgressSpin)
        dev.compilekernel.wolf_club.initColorMode(ctx)
        dev.compilekernel.wolf_club.initSiteStyles(ctx)
        dev.compilekernel.wolf_club.initTheme(ctx)
        dev.compilekernel.wolf_club.common.components.widgets.initHeaderLinkHover(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        dev.compilekernel.wolf_club.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
