package dev.compilekernel.wolf_club.common.server

import generated.Env

object ServerUrlBuilder {
    fun buildRequestUrl(endpoint: String, useWebSocket: Boolean = false): String {
        val baseUrl = Env.SERVER_BASE_URL.removeSuffix("/")
        if (useWebSocket) {
            return "${baseUrl.replaceFirst("http", "ws").replaceFirst("https", "wss")}/$endpoint"
        }

        return "${baseUrl}/$endpoint"
    }
}
