package dev.compilekernel.wolf_club.common.utils

sealed interface DataLoadState<out T> {
    data object Loading : DataLoadState<Nothing>
    data class Success<out T>(val data: T) : DataLoadState<T>
    data class Failure(val message: String) : DataLoadState<Nothing>

    val isLoading get() = this is Loading
    val isSuccess get() = this is Success
    val isFailure get() = this is Failure

    fun getErrorMessage(): String = (this as Failure).message
    fun getData(): T = (this as Success<T>).data
}
