package dev.compilekernel.wolf_club.common.localization

import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.MutableState
import androidx.compose.runtime.ReadOnlyComposable
import androidx.compose.runtime.compositionLocalOf
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import com.varabyte.kobweb.browser.dom.ElementTarget
import com.varabyte.kobweb.silk.components.overlay.PopupPlacement
import com.varabyte.kobweb.silk.components.overlay.Tooltip
import dev.compilekernel.wolf_club.common.components.widgets.IconButton
import kotlinx.browser.localStorage
import kotlinx.browser.window
import org.jetbrains.compose.web.dom.Text

private const val LOCALE_KEY = "locale"

private val DEFAULT_LOCALE = LocaleMode.EN

private val rootLocaleModeState by lazy { mutableStateOf(DEFAULT_LOCALE) }

private val LocalLocaleMode = compositionLocalOf { rootLocaleModeState }

enum class LocaleMode(val languageCode: String) {
    EN("en"), AR("ar");

    companion object {
        val currentState: MutableState<LocaleMode> @Composable get() = LocalLocaleMode.current

        val current: LocaleMode @Composable @ReadOnlyComposable get() = LocalLocaleMode.current.value

        fun fromLanguageCode(languageCode: String): LocaleMode? =
            entries.find { it.languageCode.take(2) == languageCode }

        fun fromLocalStorage(): LocaleMode? =
            window.localStorage.getItem(LOCALE_KEY)
                ?.let { languageCode -> fromLanguageCode(languageCode) }

        fun userPreferred(): LocaleMode? = entries.find { it.languageCode == window.navigator.language }

        @Composable
        fun strings(): ResStrings {
            return when(LocaleMode.current) {
                EN -> EnStrings
                AR -> ArStrings
            }
        }
    }

    fun opposite(): LocaleMode {
        return when (this) {
            EN -> AR
            AR -> EN
        }
    }
}

@Composable
fun SetupLocalization() {
    var localeMode by LocaleMode.currentState

    // The default: Local storage > User preferred > default
    LaunchedEffect(Unit) {
        (LocaleMode.fromLocalStorage()
            ?: LocaleMode.userPreferred())?.let { localeMode = it }
    }
    LaunchedEffect(localeMode) {
        localStorage.setItem(LOCALE_KEY, localeMode.languageCode)
    }
}

@Composable
fun ToggleLanguageButton() {
    var localeMode by LocaleMode.currentState

    IconButton(onClick = {
        localeMode = localeMode.opposite()
    }) {
        Text(localeMode.languageCode.uppercase())
    }
    Tooltip(ElementTarget.PreviousSibling, LocaleMode.strings().toggleLanguage, placement = PopupPlacement.BottomRight)
}
