package dev.compilekernel.wolf_club.user.data

import dev.compilekernel.wolf_club.common.utils.DateSerializer
import kotlinx.serialization.Serializable
import kotlin.js.Date

@Serializable
data class User(
    val id: String,
    val email: String,
    val info: Info,
    @Serializable(with = DateSerializer::class)
    val createdAt: Date,
    @Serializable(with = DateSerializer::class)
    val updatedAt: Date,
    val photoUrl: String?,
    val loginLink: String
) {
    companion object {
        @Serializable
        data class Info(
            @Serializable(with = DateSerializer::class)
            val birthDate: Date,
            val height: Double,
            val name: String,
            val phoneNumber: String
        )
    }
}
