import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.H1
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun ProfilePage() {
    if (rememberPageContext().isExporting) {
        return
    }
    val strings = LocaleMode.strings()

    PageLayout(LocaleMode.strings().home) {
        var state: DataLoadState<User?> by remember { mutableStateOf(DataLoadState.Loading) }
        val scope = rememberCoroutineScope()

        LaunchedEffect(Unit) {
            scope.launch {
                try {
                    val user = UserApi.instance.fetchSavedUser()
                    state = DataLoadState.Success(user)
                } catch (e: Exception) {
                    state = DataLoadState.Failure(e.message.toString())
                }
            }
        }

        when (state) {
            DataLoadState.Loading, is DataLoadState.Failure -> {
                Box(Modifier.fillMaxSize(), contentAlignment = Alignment.Center) {
                    if (state.isLoading) {
                        CircularProgressIndicator()
                    } else if (state.isFailure) {
                        state
                        Text(strings.unexpectedErrorOccurred(state.getErrorMessage()))
                    }
                }
            }

            is DataLoadState.Success<User?> -> {
                val user = state.getData()
                if (user == null) {
                    Text(strings.notLoggedInMsg)
                    return@PageLayout
                }

                UserProfile(user)
            }
        }
    }
}

@Composable
private fun UserProfile(user: User) {
    val strings = LocaleMode.strings()

    Box(
        modifier = Modifier.textAlign(TextAlign.Center)
            .margin(bottom = 20.px)
    ) {
        Column {
            user.photoUrl?.let {
                Image(
                    src = it,
                    description = "User Photo",
                    modifier = Modifier.attr("loading", "lazy"),
                    width = 128, height = 128,
                )
            }
            H1(Modifier.margin(topBottom = 0.px).toAttrs()) { Text(user.info.name) }
            P(Modifier.margin(topBottom = 0.px).toAttrs()) { Text(user.email) }

        }
    }
}
