package dev.compilekernel.wolf_club.blog.data

import dev.compilekernel.wolf_club.common.constants.ProjectInfo

data class BlogMarkdownFile(
    val title: String,
    val description: String,
    val tocMin: Int,
    val tocMax: Int,
    val author: String,
    val date: String,
    val updated: String? = null,
    val imageUrl: String? = null
) {
    companion object {
        const val DEFAULT_TOC_MIN = 2
        const val DEFAULT_TOC_MAX = 3
        const val DEFAULT_AUTHOR = ProjectInfo.PROJECT_DISPLAY_NAME
    }
}

data class BlogMarkdownFileWithRoute(val blogMarkdownFile: BlogMarkdownFile, val route: String)
