package dev.compilekernel.wolf_club.user.data

import com.varabyte.kobweb.browser.http.ResponseException
import dev.compilekernel.wolf_club.common.server.clearHttpCredentialsBasicAuth
import dev.compilekernel.wolf_club.common.server.fetchServer
import dev.compilekernel.wolf_club.common.server.setHttpCredentialsBasicAuth
import kotlinx.browser.window
import kotlinx.serialization.json.Json

class RemoteUserApi : UserApi {
    companion object {
        private const val LOGIN_TOKEN_KEY = "loginToken"
        private const val USER_KEY = "user"
    }

    override suspend fun fetchSavedUser(): User? {
        return try {
            window.localStorage.getItem(USER_KEY)?.let { User.fromJson(it) }
                ?.also { configureHttpBasicAuthHeader(it.id, requireLoginToken()) }
        } catch (e: Exception) {
            logout()
            console.error("Could not load the saved user, logging out: $e")
            null
        }
    }

    override suspend fun login(userId: String, loginToken: String): Result<Unit> = try {
        configureHttpBasicAuthHeader(userId, loginToken)

        val response = window.fetchServer(endpoint = "user")
        val user = User.fromJson(response)
        saveUser(user, loginToken)

        Result.success(Unit)
    } catch (e: Throwable) {
        Result.failure(e)
    }

    override suspend fun fetchUser(): User? = try {
        val currentUser =
            requireNotNull(fetchSavedUser()) { "The user should be already logged in to fetch the user data." }
        configureHttpBasicAuthHeader(currentUser.id, requireLoginToken())

        val response = window.fetchServer(endpoint = "user")
        val user = User.fromJson(response)
        saveUser(user, null)

        user
    } catch (_: ResponseException) {
        null
    }

    private fun saveUser(user: User, loginToken: String?) {
        window.localStorage.setItem(USER_KEY, user.toJson())
        loginToken?.let { window.localStorage.setItem(LOGIN_TOKEN_KEY, it) }
    }

    private fun configureHttpBasicAuthHeader(username: String, password: String) {
        setHttpCredentialsBasicAuth(username, password)
    }

    override suspend fun logout() {
        window.localStorage.removeItem(USER_KEY)
        window.localStorage.removeItem(LOGIN_TOKEN_KEY)
        clearHttpCredentialsBasicAuth()
    }

    private fun requireLoginToken(): String = requireNotNull(window.localStorage.getItem(LOGIN_TOKEN_KEY)) {
        "The login token is expected to be stored but appears null."
    }

    private fun User.Companion.fromJson(json: String) =
        Json.decodeFromJsonElement(serializer(), Json.decodeFromString(json))

    private fun User.toJson() = Json.encodeToString(this)
}